@import url(https://fonts.googleapis.com/css?family=Spartan:400,500&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}html,.dark{background-color:#002b36;color:#839496}html *,.dark *{color:#839496}html h1,html h2,html h3,html h4,html h5,html h6,.dark h1,.dark h2,.dark h3,.dark h4,.dark h5,.dark h6{color:#93a1a1;border-color:#839496}html a,html a:active,html a:visited,.dark a,.dark a:active,.dark a:visited{color:#93a1a1}.light{background-color:#fdf6e3;color:#657b83}.light *{color:#657b83}.light h1,.light h2,.light h3,.light h4,.light h5,.light h6{color:#586e75;border-color:#657b83}.light a,.light a:active,.light a:visited{color:#586e75}@media (min-width: 576px){.footer{display:none !important}}

